* {
  box-sizing: border-box;
}

ul {
  padding: 0;
}

/* li {
  list-style-type: none;
} */

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background: rgb(200, 238, 255) url(../img/map.png) no-repeat;
  background-size: 100%; /* Современные браузеры */
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 40px;
  border-radius: 5px;
  border: 2px solid rgb(200, 238, 255);
  margin-top: 40px;
}

#search-box:focus {
  border: 2px solid rgb(68, 57, 214);
  border-radius: 5px;
  outline: none;
}

.info__list {
  display: flex;
  align-items: center;
  margin-bottom: 20px;

  background-color: rgb(200, 238, 255);
}

.info__img {
  max-width: 100px;
}

.country__img {
  max-width: 300px;
}

.country__item {
  margin-left: 40px;
}
